
import AirIcon from '@mui/icons-material/Air';
import { WiDaySunnyOvercast, WiDaySunny, WiFog, WiHumidity, WiRainMix, WiRain, WiSnow, WiShowers, WiThunderstorm
        ,WiNightPartlyCloudy , WiNightClear } from "react-icons/wi";
import { DateTime } from "luxon";
import { useEffect, useState } from 'react';
import { nanoid } from 'nanoid';


export default function HourlyTemp(props){
    const tz = Intl.DateTimeFormat().resolvedOptions().timeZone;
    const [sunriseSunset, setSunriseSunset] = useState({
        sunrise: '',
        sunset: ''
    })
    const [sunriseSunsetTrueFalse, setSunriseSunsetTrueFalse] = useState();
    const [indexTime, setIndexTime] = useState()
    const [humidity, setHumidity] = useState('');
    const [hours, setHours] = useState([]);
    const [hoursIcons, setHoursIcons] = useState([]);
    const [hoursTemp, setHoursTemp] = useState([]);
    const [weekDaysLang, setWeekDayslang] = useState('');

    const hourMap = hours.map(el=>{
        return <div key={nanoid()} className="w-10 h-10 text-center font-bold font-roboto flex justify-center items-center text-xl">{el}</div>
    })
    
    const iconWeather = hoursIcons.map(el=>{
        return <div key={nanoid()} className="w-10 h-10 text-center font-bold font-roboto flex justify-center items-center text-xl">{el}</div>
    })
    
    const divTemp = hoursTemp.map(el=>{
        return <div key={nanoid()} className="w-10 h-10 text-center font-bold font-roboto flex justify-center items-center text-sm md:text-xl ">
            {props.tempFahrenheit ? props.fahrenheitFormula(el) : el}</div>
    })

    useEffect(()=>{
        const todayDayNumber = DateTime.now().weekday -1
        const weekDayNameEn = ['Monday','Tuesday','Wednesday','Thursday','Friday','Saturday','Sunday'];
        const weekDayNameRo = ['Luni','Mari','Miercuri','Joi','Vineri','Smbt','Duminic',];
        if(props.changeLangTrueFalse === true){
            setWeekDayslang( weekDayNameEn[todayDayNumber]);
        } else if(props.changeLangTrueFalse === false){
            setWeekDayslang( weekDayNameRo[todayDayNumber]);
        }
    },[props.changeLangTrueFalse]);
   
    const todayDayDate = DateTime.now().setLocale('en-GB').toFormat("dd/MM/yy")
    useEffect(()=>{
        if(sunriseSunset.sunrise !== '' && sunriseSunset.sunset !== ''){
            const localDate = DateTime.now().toFormat("HH:mm");
            let sunriseTrue = '';
            if(localDate > sunriseSunset.sunrise && localDate < sunriseSunset.sunset){
                sunriseTrue = true;
            }else {
                sunriseTrue = false;
            }
            setSunriseSunsetTrueFalse(sunriseTrue);
        }
        
        },[sunriseSunset.sunrise, sunriseSunset.sunset]);

      

//functie care ne arata cum este vremea actuala
    function weatherNow(){
        const code = props.currentTemp.current_weather.weathercode;
        let nowWeather = ''
        if (code === 0){
            nowWeather = <>{sunriseSunsetTrueFalse ? <WiDaySunny size="1.5em" /> : <WiNightClear size="1.5em"/>}
            {props.changeLangTrueFalse ? "Clear sky":"Cer Senin"}</>; 
        }else if (code >= 1 && code <= 3 ){
            nowWeather = <>{sunriseSunsetTrueFalse ? <WiDaySunnyOvercast size="1.5em" /> : <WiNightPartlyCloudy size="1.5em" />}
            {props.changeLangTrueFalse ? "Partly Cloudy":"Parial nnorat"}</>; 
        }else if(code >= 45 && code <= 48 ){
            nowWeather = <><WiFog size="1.5em" />{props.changeLangTrueFalse ? "Fog":"Cea"}</>; 
        }else if(code >= 51 && code <= 55 ){
            nowWeather = <><WiRainMix size="1.5em" />{props.changeLangTrueFalse ? "Drizzle":"Burni"}</> ;
        }else if(code >= 56 && code <= 57 ){
            nowWeather = <><WiRainMix size="1.5em" />{props.changeLangTrueFalse ? "Freezing Drizzle":"Burni ngheat"}</>; ;
        }else if(code >= 61 && code <= 65 ){
            nowWeather = <><WiRain size="1.5em" />{props.changeLangTrueFalse ? "Rain":"Ploaie"}</> ;
        }else if(code >= 66 && code <= 67 ){
            nowWeather = <><WiRain size="1.5em" />{props.changeLangTrueFalse ? "Freezing Rain":"Ploaie ngheat"}</>;
        }else if(code >= 71 && code <= 75 ){
            nowWeather = <> <WiSnow size="1.5em" Snow/>{props.changeLangTrueFalse ? "Snow fall":"Ninsoare"}</>;
        }else if(code === 77 ){
            nowWeather = <><WiSnow size="1.5em" />{props.changeLangTrueFalse ? "Snow":"Ninsoare"}</>; 
        }else if(code >= 80 && code <= 82 ){
            nowWeather = <><WiShowers size="1.5em" />{props.changeLangTrueFalse ? "Rain":"Ploaie"}</>;
        }else if(code >= 85 && code <= 86 ){
            nowWeather = <><WiSnow size="1.5em" />{props.changeLangTrueFalse ? "Snow":"Ninsoare"}</>; 
        }else if(code >= 95 && code <= 96  ){
            nowWeather = <><WiThunderstorm size="1.5em" />{props.changeLangTrueFalse ? "Thunderstorm":"Furtun"}</>; 
        }
        return nowWeather
    }
    
    const windSpeed = props.currentTemp.current_weather.windspeed
    
    useEffect(()=>{
        if(props.currentTemp.daily.sunrise !== undefined && indexTime !== undefined){
                function sunrise(){
                    const jsDates = DateTime.fromISO(props.currentTemp.daily.sunrise[0]).setZone(tz).o
                    return DateTime.fromISO(props.currentTemp.daily.sunrise[0]).plus({minute: jsDates-180}).toFormat("HH:mm")
                }
                function sunset(){
                    const jsDates = DateTime.fromISO(props.currentTemp.daily.sunset[0]).setZone(tz).o
                    return DateTime.fromISO(props.currentTemp.daily.sunset[0]).plus({minute: jsDates-180}).toFormat("HH:mm")
                }
                
            setSunriseSunset({  sunrise : sunrise(),
                                sunset : sunset()
                            })
        } 
       
    },[props.currentTemp.daily.sunrise, props.currentTemp.daily.sunset, indexTime, tz])

    useEffect(()=>{
        if(props.currentTemp.hourly.time === undefined && indexTime === undefined){
            return 
        } else{
            const indexOfTime = props.currentTemp.hourly.time.indexOf(props.currentTemp.current_weather.time)
            setIndexTime(indexOfTime)
        }
    },[props.currentTemp.current_weather.time, props.currentTemp.hourly.time, indexTime])
 
   
    useEffect(()=>{
        if(props.currentTemp.hourly.relativehumidity_2m !== undefined && indexTime !== undefined){
            setHumidity(props.currentTemp.hourly.relativehumidity_2m[indexTime])
        } else{
            
        }
       
    },[props.currentTemp.hourly.relativehumidity_2m, indexTime])

    useEffect(()=>{
        if(props.currentTemp.hourly.time !== undefined && indexTime !== undefined){
            const hours = props.currentTemp.hourly.time.slice(indexTime+1, indexTime+8)
            const hoursMap = hours.map(el=>{
                const jsDates = DateTime.fromISO(el).setZone(tz).o
                return DateTime.fromISO(el).plus({minute: jsDates-180}).toFormat("HH")
        })
        setHours(hoursMap)
        } 
        
    },[props.currentTemp.hourly.time, indexTime, tz])

    const trueHours = hours.map((e,)=>{
        if(e >= '20' && e <= '23'){
           return <WiNightClear size="2em"/>
            } if ( e >= '00' && e <= '06'){
            return <WiNightClear size="2em"/>
            } else return <WiDaySunny size='2em' />
   })

   const trueHoursTwo = hours.map((e,)=>{
    if(e >= '20' && e <= '23'){
       return <WiNightPartlyCloudy size="2em" />
        } if ( e >= '00' && e <= '06'){
        return <WiNightPartlyCloudy size="2em" />
        } else return <WiDaySunnyOvercast size="2em" />
    })
   
    useEffect(()=>{
        if(props.currentTemp.hourly.weathercode !== undefined && indexTime !== undefined){
            const hourlyWeatherCode = props.currentTemp.hourly.weathercode.slice(indexTime+1, indexTime+8);
            const icons = hourlyWeatherCode.map((el, i)=>{
            if (el === 0){
                return trueHours[i]
            }else if (el >= 1 && el <= 3 ){
                return trueHoursTwo[i]
            }else if(el >= 45 && el <= 48 ){
                return <WiFog size="2em" />
            }else if(el >= 51 && el <= 55 ){
                return <WiRainMix size="2em" />;
            }else if(el >= 56 && el <= 57 ){
                return  <WiRainMix size="2em" />;
            }else if(el >= 61 && el <= 65 ){
                return  <WiRain size="2em" />;
            }else if(el >= 66 && el <= 67 ){
                return  <WiRain size="2em" />;
            }else if(el >= 71 && el <= 75 ){
                return  <WiSnow size="2em" />;
            }else if(el === 77 ){
                return  <WiSnow size="2em" />;
            }else if(el >= 80 && el <= 82 ){
                return  <WiShowers size="2em" />;
            }else if(el >= 85 && el <= 86 ){
                return  <WiSnow size="2em" />;
            }else if(el >= 95 && el <= 96 ){
                return  <WiThunderstorm size="2em" />;
            }
            
           })
           setHoursIcons(icons)
        }
    },[props.currentTemp.hourly.weathercode, indexTime, hours])

   
    useEffect(()=>{
        if(props.currentTemp.hourly.temperature_2m !== undefined && indexTime !== undefined){
            setHoursTemp(props.currentTemp.hourly.temperature_2m.slice(indexTime+1, indexTime+8))
        }
    },[props.currentTemp.hourly.temperature_2m, indexTime])

    return(
        <div className="px-8 md:px-32">
            <div style={props.colorDarkMode} className="flex justify-between md:text-2xl md:justify-center md:gap-20 font-roboto font-bold">
                <div className="flex gap-2 pb-8 text-sm md:text-xl">
                    <p>{weekDaysLang}</p>
                    <p >{todayDayDate}</p>
                </div>
                <div className='flex items-start'>
                        <p className='text-sm md:text-xl flex items-center gap-1'>{weatherNow()}</p>
                </div>
                
            </div>
            <div style={props.colorDarkMode} className='flex gap-16 justify-between md:justify-center md:gap-32 pb-7 font-roboto font-bold'>
                <div className='flex items-start flex-col md:flex-row md:gap-4'>
                    <div className='flex gap-1'>
                        <div><AirIcon /></div>
                        <h1>{windSpeed}km/h</h1>
                    </div>
                    <div className='flex gap-1'>
                        <WiHumidity size="1.5em" />
                        <h1>{humidity}%</h1>
                    </div>
                </div>
                <div className='flex gap-3 md:gap-10'>
                    <div className='text-center flex flex-col md:flex-row md:gap-2'>{props.changeLangTrueFalse ? <div>Sunrise:</div> : <div>Rsrit:</div>}<div> {sunriseSunset.sunrise}</div></div>
                    <div className='text-center flex flex-col md:flex-row md:gap-2'>{props.changeLangTrueFalse ? <div>Sunset:</div> : <div>Apus:</div>} <div>{sunriseSunset.sunset}</div></div>
                </div>
            </div>
            <div style={props.colorDarkMode}>
                <div className='flex justify-between md:justify-center md:gap-12' >
                    {hourMap}
                </div>
                <div className='flex justify-between md:justify-center md:gap-12' >
                    {iconWeather}
                </div>
                <div className='flex justify-between md:justify-center md:gap-12' >
                    {divTemp}
                </div>
            </div>
                <div className='w-full md:flex md:justify-center'>
                    <div style={{borderColor: props.darkMode? "white" : "black"}} className='border-b border-black pt-3 md:w-96'></div>
                </div>
        </div>
    )
}