import React from 'react';
import { DarkModeSwitch } from 'react-toggle-dark-mode';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import { nanoid } from 'nanoid';

export default function MainTemp(props){

    
    const locations = props.locationSelect
    const locationDivs = locations.map(el=>{
        return <div key={nanoid()} id={el.id} onClick={props.divClick} className='cursor-pointer border h-10 bg-white border-black flex items-center justify-center'>
                                                            {el.name}, {el.admin1}{el.admin1 !== undefined ? ",": el.admin1} {el.country_code}</div>
    })
    const noRes = props.noResults.map(el=>{
        return <div key={nanoid()} className='cursor-pointer border h-10 bg-white border-black flex items-center justify-center'>{el}</div>
    })
    const currentTemp =  props.currentTemp;
    return(
            <div  className="px-8 pb-6">
                <div className="flex justify-around pt-6 items-center">
                        <div style={props.colorDarkMode}>
                            <DarkModeSwitch
                            checked={props.darkMode}
                            onChange={props.darkFunction}
                            size={20}
                            />
                         </div>
                    <div className='flex content-center items-center relative font-roboto pl-2'>
                          <input value={props.valueInput}
                                 onChange={e=>props.inputChangeLocation(e)}
                                 placeholder={props.changeLangTrueFalse ? "Search Location" : "Caut Locaie" }
                                 type="text" 
                                 className=' border outline-1 border-black rounded-md px-1 focus:outline-none' />
                    </div>
                            {<div className='absolute top-14 w-80 text-center font-bold'>
                                    {locationDivs.length === 0 ?<>{noRes}</> : <>{locationDivs}</>}
                                    
                            </div>}
                    <div style={props.colorDarkMode} className='flex gap-2'>
                        <div onClick={props.clickCoordonateIp} className='flex items-center hover:cursor-pointer'>
                            <LocationOnIcon />
                        </div>
                        <div onClick={props.changeLang} className='hover:cursor-pointer font-roboto text-lg'>
                            {props.changeLangTrueFalse ? <h1>EN</h1> : <h1>RO</h1>}
                        </div>
                    </div>
                </div>
                <div  style={props.colorDarkMode}
                    className='text-3xl flex justify-center pt-5 font-roboto text-center font-bold'>{props.judOras[0].oras}{props.judOras[0].jud !== undefined ? "," : props.judOras[0].jud} {props.judOras[0].jud}</div>
                <div style={props.colorDarkMode} className='flex justify-center items-center gap-2 pt-3 md:pt-0'>
                    <div className='text-8xl flex justify-center font-roboto text-center font-bold md:pt-0'>{props.tempFahrenheit ? props.fahrenheitFormula(currentTemp) : currentTemp}</div>
                    <div className='hover:cursor-pointer' onClick={props.tempToFahrenheit}>
                        <div className='text-8xl font-roboto'><div className='text-4xl m-0 font-extrabold'></div>{props.tempFahrenheit ? <div className='text-3xl font-bold'>F</div> : <div className='text-3xl font-bold'>C</div>} </div>
                    </div>
                </div>
               
                
            </div>
    )
}